<template>
  <div class="home">
    <div class="block">
      <el-carousel :height="height">
        <el-carousel-item v-for="item in imageList" :key="item">
          <img :src="item" alt="" width="100%" height="100%">
        </el-carousel-item>
      </el-carousel>
    </div>

  </div>
</template>
<script>

export default {
  data() {
    return {
      imageList: [
        require('../../../static/sliderImg/index-large-01.jpg'),
        require('../../../static/sliderImg/index-large-02.jpg'),
        require('../../../static/sliderImg/index-large-03.jpg')
      ],
      height:  '300px'
    };
  },
  components: {},
  mounted() {
  },
  created() {
    var wheight = document.documentElement.clientHeight - 72 + 'px'
    this.height = wheight;
  },
  methods: {
    
  }
};
</script>
<style lang="scss" scoped>
.home {
  min-height: 500px;
}

.el-carousel__item h3 {
  color: #475669;
  font-size: 14px;
  opacity: 0.75;
  line-height: 150px;
  margin: 0;
}

.el-carousel__item:nth-child(2n) {
  background-color: #99a9bf;
}

.el-carousel__item:nth-child(2n + 1) {
  background-color: #d3dce6;
}
</style>




