<template>
  <div :style="bgStyle">
    <el-row>
      <el-col>
        <img :src="url[0]" alt="" height="100%" width="100%">
      </el-col>
      <el-col>
        <el-carousel :interval="4000" type="card" height="300px">
          <el-carousel-item v-for="item in sketchList" :key="item">
            <div class="pic-wrapper" height="300px">
              <img v-lazy="item" alt="" height="300px" @click="openPic(item)">
            </div>
          </el-carousel-item>
        </el-carousel>
      </el-col>
    </el-row>
    <el-row>
      <el-col>
        <img :src="url[1]" alt="" height="100%" width="100%">
      </el-col>
      <el-col>
        <el-carousel :interval="4000" type="card" height="300px">
          <el-carousel-item v-for="item in photoList" :key="item">
            <div class="pic-wrapper" height="300px">
              <img v-lazy="item" alt="" height="300px" @click="openPic(item)">
            </div>
          </el-carousel-item>
        </el-carousel>
      </el-col>
    </el-row>

    <el-row>
      <el-col>
        <img :src="url[2]" alt="" height="100%" width="100%">
      </el-col>
      <el-col>
        <el-carousel :interval="4000" type="card" height="300px">
          <el-carousel-item v-for="item in storyBoardList" :key="item">
            <div class="pic-wrapper" height="300px">
              <img v-lazy="item" alt="" height="300px" @click="openPic(item)">
            </div>
          </el-carousel-item>
        </el-carousel>
      </el-col>
    </el-row>

    <el-row>
      <el-col>
        <img :src="url[3]" alt="" height="100%" width="100%">
      </el-col>
      <el-col>
        <el-carousel :interval="4000" type="card" height="300px">
          <el-carousel-item v-for="item in digitalList" :key="item">
            <div class="pic-wrapper" height="300px">
              <img v-lazy="item" alt="" height="300px" @click="openPic(item)">
            </div>
          </el-carousel-item>
        </el-carousel>
      </el-col>
    </el-row>

    <el-row>
      <el-col>
        <img :src="url[4]" alt="" height="100%" width="100%">
      </el-col>
      <el-col>
        <el-carousel :interval="4000" type="card" height="300px">
          <el-carousel-item v-for="item in renderList" :key="item">
            <div class="pic-wrapper" height="300px">
              <img v-lazy="item" alt="" height="300px" @click="openPic(item)">
            </div>
          </el-carousel-item>
        </el-carousel>
      </el-col>
    </el-row>

    <el-dialog :visible.sync="centerDialogVisible" class="pic-detail" width="60%" center>
      <!-- <span>需要注意的是内容是默认不居中的</span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="centerDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="centerDialogVisible = false">确 定</el-button>
      </span> -->
      <img :src="detailPic" alt="" width="90%">
    </el-dialog>
  </div>
</template>
<script>
import imgUrl from "./imgUrl";
export default {
  data() {
    return {
      centerDialogVisible: false,
      detailPic: "",
      url: imgUrl.url,
      sketchList: imgUrl.sketchList,
      photoList: imgUrl.photoList,
      storyBoardList: imgUrl.storyBoardList,
      digitalList: imgUrl.digitalList,
      renderList: imgUrl.renderList,
      bgStyle: {
        backgroundImage:
          "url(" + require("./images_bg.jpg") + ")"
      }
    };
  },
  methods: {
    openPic(url) {
      this.detailPic = url;
      this.centerDialogVisible = true;
    }
  }
};
</script>

<style lang="scss">
.el-carousel__item h3 {
  color: #475669;
  font-size: 14px;
  opacity: 0.75;
  line-height: 300px;
  margin: 0;
}
.pic-wrapper {
  text-align: center;
}
.images {
  background: url("images_bg.jpg");
}

.el-carousel__item:nth-child(2n) {
  background: none;
}
.pic-detail {
  img {
    margin: 0 auto;
  }
}
</style>




