<template>
  <div class="videos">
    <div class="block">
      <el-carousel height="650px">
        <el-carousel-item :style="bgStyle[0]">
          <el-row :gutter="10">
            <el-col :span="12">
              <img :src="imageList[0].left" alt="">
            </el-col>
            <el-col :span="12">
              <el-row>
                <el-col :span="20" :offset="2">
                  <a href="https://player.vimeo.com/video/192601903" target="_blank">
                    <img :src="imageList[0].play" alt="">
                  </a>
                </el-col>
                <el-col :span="20" :offset="2">
                  <img :src="imageList[0].text" alt="">
                </el-col>
              </el-row>
            </el-col>
          </el-row>
        </el-carousel-item>

        <el-carousel-item :style="bgStyle[1]">
          <el-row>
            <el-col :span="14">
              <el-row>
                <el-col :span="18" :offset="3">
                  <a href="https://player.vimeo.com/video/192464930" target="_blank">
                    <img :src="imageList[1].play" alt="">
                  </a>
                </el-col>
              </el-row>
              <el-row :span="14">
                <el-col :span="24">
                  <img :src="imageList[1].left" alt="">
                </el-col>
              </el-row>
            </el-col>
            <el-col :span="10">
              <img :src="imageList[1].text" alt="" class="trace-text">
            </el-col>
          </el-row>
        </el-carousel-item>

        <el-carousel-item :style="bgStyle[2]">
          <el-row :gutter="10">
            <el-col :span="12">
              <img :src="imageList[2].left" alt="">
            </el-col>
            <el-col :span="12">
              <el-row>
                <el-col :span="20" :offset="2">
                  <a href="https://player.vimeo.com/video/194624158" target="_blank">
                    <img :src="imageList[2].play" alt="">
                  </a>
                </el-col>
                <el-col :span="20" :offset="2">
                  <img :src="imageList[2].text" alt="">
                </el-col>
              </el-row>
            </el-col>
          </el-row>
        </el-carousel-item>
      </el-carousel>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      imageList: [
        {
          bg: require("../../../static/sliderImg/cans_bg.jpg"),
          left: require("../../../static/sliderImg/cans_left.jpg"),
          play: require("../../../static/sliderImg/cans_play.jpg"),
          text: require("../../../static/sliderImg/cans_text.png")
        },
        {
          bg: require("../../../static/sliderImg/trace_bg.jpg"),
          left: require("../../../static/sliderImg/trace_left.jpg"),
          play: require("../../../static/sliderImg/trace_play.jpg"),
          text: require("../../../static/sliderImg/trace_text.png")
        },
        {
          bg: require("../../../static/sliderImg/jieguangji_bg.jpg"),
          left: require("../../../static/sliderImg/jieguangji_left.jpg"),
          play: require("../../../static/sliderImg/jieguangji_play.jpg"),
          text: require("../../../static/sliderImg/jieguangji_text.png")
        }
      ],
      bgStyle: [
        {
          backgroundImage:
            "url(" + require("../../../static/sliderImg/cans_bg.jpg") + ")",
          padding: "20px"
        },
        {
          backgroundImage:
            "url(" + require("../../../static/sliderImg/trace_bg.jpg") + ")",
          padding: "20px"
        },
        {
          backgroundImage:
            "url(" +
            require("../../../static/sliderImg/jieguangji_bg.jpg") +
            ")",
          padding: "20px"
        }
      ]
    };
  },
  components: {},
  mounted() {}
};
</script>
<style lang="scss" scoped>
.videos {
  min-height: 650px;
}

.el-carousel__item h3 {
  color: #475669;
  font-size: 14px;
  opacity: 0.75;
  line-height: 150px;
  margin: 0;
}

img {
  width: 100%;
}
.trace-text {
  margin-top: 80px;
}
</style>





