<template>
  <header class="head">
    <div class="avatar">
      <img src="./avatar.png" alt="头像">
    </div>
    <div class="head-nav">
      <router-link v-for="(item, index) in navList" :key="item.name" :to="{path: `/${item.path}`}" 
        class="nav-item" :class="{active: selected == index}" @click.native="changeNav(index)">{{item.name}}</router-link>
      <!-- <li v-for="item in langList" :key="item">{{item}}</li> -->
    </div>
    <div class="language">
      <div class="lang">
        <span class="en" :class="{active: lang == 'en'}" @click="changeLang('en')"></span>
        <span class="cn" :class="{active: lang == 'cn'}" @click="changeLang('cn')"></span>
      </div>
    </div>
  </header>
</template>
<script>
import localIndex from '../../assets/js/utils'

export default {
  data() {
    return {
      //   navList: ["Lucas Peng", "Introduction", "Videos", "Images", "Other"],
      navList: [
        {
          name: "Lucas Peng",
          path: "home"
        },
        {
          name: "Introduction",
          path: "introduction"
        },
        {
          name: "Videos",
          path: "videos"
        },
        {
          name: "Images",
          path: "images"
        },
        // {
        //   name: "Other",
        //   path: "other"
        // }
      ],
      langList: ["EN", "简体中文"],
      selected: null,
      lang: "en"
    };
  },
  created(){
    this.selected = localIndex.getNavIndex();
  },
  methods: {
    changeNav(index) {
      localIndex.setNavIndex(index);
      this.selected = localIndex.getNavIndex();
      
    },
    changeLang(value) {
      if (value == this.lang) {
        return;
      } else {
        this.lang = value;
      }
    }
  }
};
</script>

<style lang="scss" scoped>
header {
  height: 6rem;
  display: flex;
  justify-content: space-around;
  background: #24191b;
  .avatar {
    width: 6%;
    img {
      height: 5rem;
      border-radius: 50%;
      height: 100%;
      border: 0.2rem solid #cccccc;
    }
  }
  .head-nav {
    display: flex;
    justify-content: space-around;
    width: 70%;
    font-size: 2.8rem;
    line-height: 5.5rem;
    a {
      &:link {
        color: #494c4d;
      }
      &:visited {
        color: #494c4d;
      }
      &:hover {
        color: #00cef8;
        text-decoration: underline;
      }
      &:active {
        color: #494c4d;
      }
      &.active {
        text-decoration: underline;
        color: #73a9af;
      }
    }
  }
  .language {
    font-size: 1.6rem;
    line-height: 5.6rem;
    span {
      cursor: pointer;
      &:hover {
        color: #00cef8;
        text-decoration: underline;
      }
      &.active {
        text-decoration: underline;
        color: #73a9af;
      }
    }
    .en {
      margin-right: 1rem;
    }
  }
}
</style>



