<template>
  <div class="front">
    <head-top></head-top>
    <transition>
      <router-view></router-view>
    </transition>
    <foot></foot>
  </div>
</template>

<script>
import headTop from "@/components/header/head";
import foot from "@/components/footer/foot";
export default {
  data() {
    return {};
  },
  components: {
    headTop,
    foot
  }
};
</script>
<style lang="scss">
.front {
  display: flex;
  flex-direction: column;
  height: 100%;
}
header {
  flex: 0 0 auto;
}
footer {
  flex: 0 0 auto;
}
</style>



